
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "HelloWorld",
  props: {
    link: {
      type: String,
      required: true
    },
    desc: {
      type: String,
      required: true
    }
  },
  setup(props) {
    // https://player.vimeo.com/video/232439098
    const videoUrl = ref(props.link);
    const description = ref(props.desc);
    return {
      videoUrl,
      description
    };
  },
});
